.box{
   background-color: #fff;
   margin-bottom: 50px;
   width: 80%;
   border-radius: 1rem 1rem;
   box-shadow: 0px 5px 30px rgba(0,0,0,0.2);
}
.upper{
   width: 100%;
   height: 300px;
   background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url('../college_02.jpg');
   background-position: center;
   background-size: cover;
   position: relative;
}
.bestcollege, .bestcollege2{
   background-color: rgba(255, 255, 255, 0.925);
   display: inline-block;
   padding: .5rem 1rem;
   border-radius: 2rem;
   color: rgb(116, 116, 116);
   position: absolute;
   bottom: 10px;
   left: 10px;
}
.promoted{
   position: absolute;
   clip-path: polygon(100% 0, 100% 100%, 0 100%, 22% 49%, 0 0);
   color: #fff;
   background-color: rgb(0, 140, 255);
   padding: .1rem 1rem;
   top: -20px;
   right: 0px;
   display: flex;
   align-items: center;
   justify-content: center;
}
.promoted p{
   padding-left: 20px;
   padding-top: .5rem;
   display: inline-block;
}
.up-box{
   width: 80px;
   height: 70px;
   position: absolute;
   top: 50px;
   right: 10px;
   background-color: orange;
   border-radius: 15px;
   color: #fff;
   display: flex;
   justify-content: space-around;
   flex-direction: column;
   align-items: center;
}
.up-box h3{
   font-size: 1.5rem;
   display: inline-block;
}
.up-box h2{
   width: 100%;
   font-size: .8rem;
   display: inline-block;
   text-align: center;
}

.bestcollege2{
   left: 200px;
}
.ranking{
   position: absolute;
   bottom: 5px;
   right: 10px;
}
.ranking p{
   color: #fff;
   font-weight: bolder;
}



/* ====== LOWER ======*/

.lower{
   width: 100%;
   height: 200px;
   display: flex;
   justify-content: space-between;
   padding: 1rem;
}
.left{
   width: 100%;
   display: flex;
   justify-content: space-between;
   flex-direction: column;
}
.left h1{
   font-size: 1.3rem;
   font-weight: bold;
}
.left h3{
   font-size: .8rem;
}
.left h3 span{
   color: rgb(139, 139, 139);
}
.left p{
   font-size: .8rem;
   color: rgb(107, 107, 107);
   font-weight: bold;
}
.left p span{
   color: rgb(4, 196, 132);
}


.flat{
   width: 95%;
   display: inline-block;
   padding: 1rem 1rem 1rem 1rem;
   background-color: rgb(3, 172, 121);
   border-radius: 0rem 2rem 2rem 0rem;
   font-size: .85rem;
   font-weight: bolder;
}


.right{
   width: 300px;
   display: flex;
   justify-content: space-between;
   flex-direction: column;
   align-items: flex-end;
}

.discount{
   display: flex;
   justify-content: center;
   align-items: center;
}
.discount p{
   display: inline-block;
   text-decoration: line-through;
}
.discount h3{
   clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
   background-color: red;
   font-size: 1rem;
   padding: .2rem 1rem;
   color: #fff;
   margin-left: .5rem;
}
.discountedfees h1{
   color: red;
   font-size: 30px;
}
.feecycle h3{
   font-size: .7rem;
   color: rgb(119, 119, 119);
}
.wifi h3{
   color: rgb(4, 196, 132);
   font-size: 1rem;
}